require('bootstrap');

import Splide from '@splidejs/splide';

import 'filepond/dist/filepond.min.css';

const galleries = document.querySelectorAll('.tab-pane .row .col-lg-7.mt-4');

const galleryImages = document.querySelectorAll('#gallery-images');

galleryImages.forEach((image) => {
  const main = new Splide( '#' + image.firstElementChild.id, {
    type      : 'fade',
    pagination: false,
    lazyLoad: 'sequential',
    heightRatio : 0.5,
  });
  main.mount();
});

galleries.forEach((gallery) => {
  const main = new Splide( '#' + gallery.firstElementChild.id, {
    type      : 'fade',
    pagination: false,
    lazyLoad: 'sequential',
  });

  const thumbnails = new Splide( '#' + gallery.lastElementChild.id, {
    fixedWidth  : 100,
    fixedHeight : 60,
    gap         : 10,
    arrows: false,
    pagination: false,
    isNavigation: true,
    lazyLoad: 'sequential',
    breakpoints : {
      600: {
        fixedWidth : 60,
        fixedHeight: 44,
      },
    },
  });
  main.sync( thumbnails );
  main.mount();
  thumbnails.mount();
});
